.header{
  height: 71px;
  background: white;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 1px 2px 10px #00000029;
  border-radius: 5px;
  margin-top: 5px;
  .right{
    display: flex;
    align-items: center;
    height: 100%;
    padding-right: 15px;
    .avatar{
      width: 50px;
      height: 50px;
      border-radius: 50%;
      border: 1px solid #ddd;
    }

    .user-name{
      color: #222222;
      background: transparent;
      border: tan;
      font-size: 16px;
    }

    .notification{
      position: relative;
      .badge{
        position: absolute;
        background: #ED639E;
        color: white;
        font-size: 9px;
        padding: 3px 6px;
        left: 10px;
        top: -5px;
      }
    }

    .line{
      border-left: 1px solid #707070;
      height: 100%;
      margin: 0px 15px;
    }

    .menu-item{
      display: flex;
      align-items: center;
      ul{
        list-style-type: none;
        padding-right: 0px;
        margin-bottom: 0px;
        display: flex;
        li{
          background: transparent;
          color: #271E70;
          height: 45px;
          font-size: 16px;
          padding: 10px 15px;
          text-align: center;
          border-radius: 5px;
          font-weight: bolder;
          img{
            margin-left: 10px;
          }
          &:hover{
            background: #271E70;
            color: white;
            cursor: pointer;
          }
        }
      }
    }
  }

  .left{
    display: flex;
    align-items: center;
    height: 100%;
    flex-direction: row-reverse;
    padding-left: 15px;
    .logo{
      margin-right: 10px;
    }

    .line{
      border-left: 1px solid #707070;
      height: 100%;
      margin: 0px 15px;
    }

    .search{
      .input-group-text{
        background-color: #FCFCFC;
        border-bottom-left-radius: 25px;
        border-top-left-radius: 25px;
        height: 40px;
      }
      .form-control{
        background-color: #FCFCFC;
        border-top-right-radius: 25px;
        border-bottom-right-radius: 25px;
        border-left: none;
        height: 40px;
      }
    }
  }
}
