.sidebar{
  box-shadow: 1px 2px 10px #00000029;
  background: white;
  border-radius: 5px;
  padding: 15px;
  text-align: right;
  list-style-type: none;
  margin-top: 10px;
  width: 100%;
  li{
    font-size: 14px;
    height: 40px;
    font-weight: bolder;
    img{
      margin-left: 10px;
    }

    &:hover{
      cursor: pointer;
    }
  }
}
